@font-face {
  font-family: 'WorkSans-Light';
  src: url("/src/assets/fonts/Work_Sans/static/WorkSans-Light.ttf");
}

@font-face {
  font-family: 'WorkSans-Medium';
  src: url("/src/assets/fonts/Work_Sans/static/WorkSans-Medium.ttf");
}

@font-face {
  font-family: 'WorkSans-Bold';
  src: url("/src/assets/fonts/Work_Sans/static/WorkSans-Bold.ttf");
}

@font-face {
  font-family: 'HachiMaruPop-Regular';
  src: url("/src/assets/fonts/Hachi_Maru_Pop/HachiMaruPop-Regular.ttf");
}

:root {
  --rosy-brown: hsl(0, 25%, 80%);
  --rosy-brown--darker: hsl(0, 25%, 70%);
  --white: hsl(231, 20%, 100%);
  --black: hsl(0, 0%, 10%);
  --shadow-top-color: hsl(0, 25%, 74%);
  --shadow-bottom-color: hsl(0, 25%, 87%);
  --main-color: var(--black);
  --secondary-color: var(--white);
  --main-bg: var(--rosy-brown);
  --main-bg--darker: var(--rosy-brown--darker);
  --primary-font--light: WorkSans-Light;
  --primary-font--medium: WorkSans-Medium;
  --primary-font--bold: WorkSans-Bold;
  --secondary-font--reg: HachiMaruPop-Regular;
  --font-size--default: 1rem;
  --font-size--medium: 2rem;
  --font-size--large: 5rem;
  --header-height: 58.5px;
}

* {
  margin: 0;
  padding: 0;
  border: 0;
  outline: none;
}

a {
  color: inherit;
  text-decoration: none;
}

li {
  list-style: none;
}

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*::-moz-selection {
  background-color: var(--main-bg--darker);
  color: var(--white);
}

*::selection {
  background-color: var(--main-bg--darker);
  color: var(--white);
}

body {
  height: 100vh;
  font-family: var(--primary-font--light);
  background-color: var(--main-bg);
  color: var(--main-color);
  overflow-x: hidden;
}

.btn {
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  padding: 1% 5%;
  background-color: inherit;
  cursor: pointer;
  -webkit-box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
}

.btn--active, .btn:active {
  -webkit-box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
}

.no-visible-scrollbar {
  overflow-y: scroll;
  -ms-overflow-style: none;
  /* IE and Edge */
  scrollbar-width: none;
  /* Firefox */
}

.no-visible-scrollbar::-webkit-scrollbar {
  display: none !important;
}

.no-display {
  display: none !important;
}

.pending {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  z-index: 10;
  background-color: rgba(217, 191, 191, 0.5);
}

.pending::before {
  --circle-radius: 100px;
  -webkit-box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
  -webkit-animation: rotate 1s linear infinite;
          animation: rotate 1s linear infinite;
}

.pending::after {
  --circle-radius: 60px;
  -webkit-box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
  -webkit-animation: rotate 0.7s linear infinite;
          animation: rotate 0.7s linear infinite;
}

.pending::before, .pending::after {
  content: '';
  position: absolute;
  width: var(--circle-radius);
  height: var(--circle-radius);
  left: calc(50% - (var(--circle-radius) / 2));
  top: calc(50% - (var(--circle-radius) / 2));
  background-color: var(--main-bg);
  border-radius: 50%;
}

@-webkit-keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.not-found {
  position: fixed;
  width: 100%;
  height: 100%;
  left: 0;
  top: 0;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}

.location-404,
.location-home {
  font-size: 8rem;
  font-weight: bolder;
  color: var(--main-bg--darker);
  text-shadow: -3px -1px 1px var(--shadow-top-color), -4px -2px 1px var(--shadow-bottom-color);
  text-align: center;
}

@media screen and (max-width: 450px) {
  .location-404,
  .location-home {
    font-size: 4rem;
  }
}

.header {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  width: 70%;
  height: var(--header-height);
  padding: 1% 5%;
  position: relative;
  z-index: 10;
  margin-left: auto;
  -webkit-box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
}

.header > div:nth-child(2) {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
  width: 100%;
}

.header > div:nth-child(2) > * {
  font-variant: small-caps;
}

@media screen and (max-width: 600px) {
  .header {
    width: 100%;
  }
}

.scripture-view {
  --this-height: calc(100vh - var(--header-height));
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 70vw;
  height: calc(var(--this-height) - 20px);
  padding-top: 20px;
  margin: 0 auto;
}

.view-direction {
  position: fixed;
  height: -webkit-max-content;
  height: -moz-max-content;
  height: max-content;
  top: 40%;
  padding: 5px 15px;
  rotate: 90deg;
}

.view-direction.prev {
  left: 0;
}

.view-direction.next {
  right: 0;
}

.viewing-passage {
  position: relative;
  padding: 1% 5%;
  width: 100%;
  cursor: default;
  word-spacing: 100%;
  line-height: 190%;
}

.viewing-passage > p:first-child {
  display: none;
}

.viewing-passage:hover {
  -webkit-box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
}

.book-select {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  width: 100%;
}

.book-select__controls {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 20px;
}

.book-select__name-control, .book-select__chapter-control {
  padding: 5px 15px;
  font-size: 1.4rem;
  font-variant: small-caps;
}

.book-select__name-control::first-letter {
  font-size: 1.6rem;
}

.others-options {
  position: absolute;
  width: 100%;
  height: calc(100vh - var(--header-height));
  left: 0;
  top: var(--header-height);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 2px;
  padding: 10px 2px 0;
  -webkit-box-shadow: inset 2px 2px 4px #cdacac, inset -2px -2px 4px #e2cfcf;
          box-shadow: inset 2px 2px 4px #cdacac, inset -2px -2px 4px #e2cfcf;
}

.others-chapter-options, .others-name-options {
  width: 100%;
  text-align: center;
  padding: 0 5px;
}

.others-chapter-options > *, .others-name-options > * {
  padding: 10px 5px;
  margin: 2px auto 5px;
  font-size: 1.5rem;
}

.others-chapter-options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
}

.book-select__options {
  position: absolute;
  width: 100%;
  height: calc(100vh - var(--header-height));
  left: 0;
  top: var(--header-height);
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 2px;
  padding: 10px 2px 0;
  -webkit-box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
  background-color: var(--main-bg);
  z-index: 10;
  overflow: hidden;
}

.book-select__chapter-options, .book-select__name-options {
  width: 100%;
  text-align: center;
  padding: 5px;
}

.book-select__chapter-options {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 10px;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  height: 100%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  padding: 10px;
}

.book-select__name-option {
  position: relative;
  width: 100%;
  padding: 10px 5px;
  margin-bottom: 10px;
  font-size: 1.5rem;
  -webkit-animation: entry 1s linear both;
          animation: entry 1s linear both;
}

.book-select__name-option::after {
  content: '';
  position: absolute;
  width: 90%;
  height: 80%;
  left: 5%;
  top: 10%;
  -webkit-box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
}

.book-select__name-option.btn--active {
  font-weight: bolder;
}

.book-select__name-option.btn--active::after {
  -webkit-box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: inset 2px 2px 4px var(--shadow-top-color), inset -2px -2px 4px var(--shadow-bottom-color);
}

.book-select__chapter-option {
  padding: 5px 15px;
  font-size: 1.2rem;
  -webkit-animation: entry 1s linear both;
          animation: entry 1s linear both;
}

@-webkit-keyframes entry {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes entry {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.votd__control {
  padding: 5px 10px;
}

.votd__content {
  position: absolute;
  width: 100%;
  height: calc(100vh - var(--header-height));
  left: 0%;
  top: 100%;
  border-radius: 0 0 0 5px;
  padding: 0px var(--font-size--medium) 10px;
  background-color: var(--main-bg);
  -webkit-box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
          box-shadow: 2px 2px 4px var(--shadow-top-color), -2px -2px 4px var(--shadow-bottom-color);
  -webkit-transition: height 0.5s ease-in-out;
  transition: height 0.5s ease-in-out;
}

.votd__content.no-height {
  height: 0;
  padding-bottom: 0;
}

.votd__header {
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  text-align: center;
  padding-top: 10px;
  font-family: var(--secondary-font--reg);
  background-color: inherit;
}

.votd__textlist {
  font-size: 1.2rem;
  line-height: 150%;
  font-variant: normal;
}

.votd__text {
  list-style: circle;
  margin: 20px;
}

.votd__passage {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  gap: 5px;
  font-weight: bold;
}

.votd__passage-msg {
  font-size: 1rem;
}
